import * as R from 'ramda'
import { v4 as uuidv4 } from 'uuid'

import webJsSDK from '@hk01-digital/web-js-sdk'
import webJsSDKStg from '@hk01-digital/web-js-sdk/dist/stg/esm'

import { ActionType, CategoryType } from 'src/types/Tracking'
import { isBrowserEnv } from 'src/utils/browser'
import uuidStorage from 'src/utils/uuidStorage'

export const SESSION_KEYS = {
  SESSION_ID: 'hk01_session_id'
}

const DEFAULT_COOKIE_TIMEOUT = 60 * 30 // 30 mins expire time

const cachedSessionId = uuidStorage.readFromCookie(
  SESSION_KEYS.SESSION_ID,
  null
)

const initConfig = {
  appId: process.env.HK01_SSO_APP_ID,
  appVersion: '0.1',
  service: process.env.SSO_SERVICE,
  baseUri: process.env.SSO_BASE_URI,
  baseApiUri: process.env.SSO_BASE_API_URI,
  sessionId: cachedSessionId
}

class WebJsSdkService {
  sdk
  trackerOpts
  constructor(trackerOpts = {}) {
    this.trackerOpts = trackerOpts

    this.sdk = null
  }

  initSdk = async () => {
    const isBrowser = isBrowserEnv()

    const WebJsSdk = isBrowser
      ? process.env.APP_ENV === 'production'
        ? webJsSDK
        : webJsSDKStg
      : null

    if (!initConfig.sessionId) {
      const newId = uuidv4()

      initConfig.sessionId = newId

      uuidStorage.saveToCookie(
        SESSION_KEYS.SESSION_ID,
        newId,
        DEFAULT_COOKIE_TIMEOUT
      )
    }

    if (!isBrowser) {
      return null
    }

    const sdk = new WebJsSdk(initConfig, {
      GA: {
        trackingId: [process.env.GA_TRACKING_ID, process.env.GA4_TRACKING_ID]
      },
      Piwik: {
        trackingUrl: process.env.PIWIK_TRACKING_URL,
        siteId: process.env.PIWIK_SITE_ID
      },
      category: process.env.TRACKING_CATEGORY,
      webviewBridgeEnabled: true,
      ...this.trackerOpts
    })

    try {
      await sdk.init()
    } catch (error) {
      throw new Error(`{error on jssdk init | ${R.toLower(error.message)}}`)
    }

    return sdk
  }

  getSdk = async () => {
    if (!this.sdk) {
      this.sdk = await this.initSdk()
    }

    return this.sdk
  }

  isWebview = async () => {
    const sdk = await this.getSdk()

    return sdk.getPlatform() === 'webview'
  }

  pageView = async (url = null) => {
    const sdk = await this.getSdk()

    const tracker = sdk.trackerClient

    if (url) {
      /**
       * @see https://github.com/hk01-digital/js-aux/tree/development/packages/data-tracker-client#usage
       * send custom url pageview tracking
       */
      tracker.pageView(
        {
          GA: true,
          Piwik: true
        },
        {},
        url
      )
    } else {
      tracker.pageView({
        GA: true,
        Piwik: true
      })
    }
  }

  getTracker = async () => {
    const sdk = await this.getSdk()

    return sdk.trackerClient
  }

  track = (
    category: CategoryType,
    action: ActionType,
    labels: Record<string, string | number>
  ) => {
    const labelData = {
      account_id: null,
      web_session_id: initConfig.sessionId,
      ts: Math.floor(Date.now()),
      ...labels
    }

    const target = {
      GA: true,
      Piwik: true
    }

    this.getTracker().then(trackerClient => {
      trackerClient.fire(target, {
        category: category,
        action,
        label: JSON.stringify(labelData)
      })
    })
  }

  tracker = async (category: CategoryType, action: ActionType, labels) => {
    const sdk = await this.getSdk()

    const tracker = sdk.trackerClient

    const labelData = {
      account_id: null,
      web_session_id: initConfig.sessionId,
      ts: Math.floor(Date.now()),
      ...labels
    }

    const defaultTrackingOption = {
      GA: true,
      Piwik: true
    }

    tracker.fire(defaultTrackingOption, {
      category: category,
      action,
      label: JSON.stringify(labelData)
    })
  }
}

export default WebJsSdkService

export const tracker = {}
